var render = function () {var _vm=this;var _h=_vm.$createElement;var _c=_vm._self._c||_h;return _c('div',{staticClass:"curso-main-container pb-3"},[_c('BannerInterno',{attrs:{"subTitulo":'1. Jurisdicción y función jurisdiccional del Estado'}}),_c('div',{staticClass:"container tarjeta tarjeta--blanca p-4 p-md-5 overflow-hidden"},[_c('p',{attrs:{"data-aos":"fade-down"}},[_vm._v("Para conocer qué es la jurisdicción y la función jurisdiccional del Estado, lo invitamos a ver el siguiente video.")]),_vm._m(0),_vm._m(1),_vm._m(2),_vm._m(3),_vm._m(4),_vm._m(5),_vm._m(6),_vm._m(7),_vm._m(8),_c('p',{attrs:{"data-aos":"fade-down"}},[_vm._v("Conozcamos estas características:")]),_c('div',{staticClass:"row align-items-start mb-5",attrs:{"data-aos":"fade-left"}},[_vm._m(9),_c('div',{staticClass:"col-lg-8"},[_c('AcordionA',{attrs:{"tipo":"b"}},[_c('div',{staticClass:"div",attrs:{"titulo":"La exclusividad la jurisdicción"}},[_c('p',[_vm._v("Lo que implica que únicamente el Estado tiene la facultad de administrar justicia y dirimir los conflictos jurídicos entre las personas. Esta exclusividad está consagrada en el Artículo 116 de la Constitución Política, el cual establece que la función de administrar justicia se ejerce por jueces y magistrados integrantes de las distintas jurisdicciones, así como por otras autoridades judiciales expresamente autorizadas por la Ley. Aunque existen mecanismos alternativos como la conciliación, el arbitraje y los jueces de paz, estos operan bajo el marco del sistema legal y están subordinados a los principios constitucionales y procesales. Por ejemplo, en un conflicto por el pago de una deuda, solo un juez civil o un árbitro habilitado legalmente puede dictar una decisión que tenga fuerza obligatoria y pueda ejecutarse coactivamente. Ninguna persona, por muy afectada que se sienta, puede hacer justicia por mano propia, porque ello implicaría sustituir ilegalmente la función jurisdiccional del Estado, incurriendo incluso en conductas punibles como la usurpación de funciones públicas o la violencia por vías de hecho.")])]),_c('div',{staticClass:"div",attrs:{"titulo":"La indelegabilidad de la jurisdicción"}},[_c('p',[_vm._v("Es una consecuencia directa de su exclusividad. Este principio significa que el juez o magistrado, una vez asignado a un proceso, no puede delegar la función de juzgar ni el dictado de la sentencia a otra persona o autoridad. La indelegabilidad garantiza que la decisión judicial sea adoptada por quien tuvo contacto directo con las partes, con las pruebas y con el desarrollo del proceso. Esta regla se conecta con el principio de inmediación, que obliga al juez a estar presente en etapas claves del juicio, como la audiencia de práctica de pruebas, para valorar directamente la credibilidad de los testigos o la autenticidad de los documentos. Por ejemplo, en un proceso penal, el juez que preside la audiencia de juicio oral es el mismo que debe dictar el fallo, debido a que fue quien conoció personalmente la actuación y escuchó los alegatos de las partes. No sería válido, ni legal, que otro juez firmara la sentencia con base en grabaciones o informes, porque se perdería la autenticidad e imparcialidad de la decisión (Garzón Vallejo, 2007).")])])])],1)]),_c('div',{staticClass:"row align-items-start mb-5",attrs:{"data-aos":"fade-left"}},[_c('div',{staticClass:"col-lg-8 mb-3 mb-lg-0"},[_c('AcordionA',{attrs:{"tipo":"b"}},[_c('div',{staticClass:"div",attrs:{"titulo":"La unidad de la jurisdicción"}},[_c('p',[_vm._v("Lo que significa que, aunque el sistema judicial esté dividido en varias jurisdicciones como la ordinaria, la contenciosa administrativa, la constitucional, la indígena, la penal militar, entre otras, todas ellas hacen parte de un único poder público: la rama judicial del Estado. Esta unidad se manifiesta tanto en el respeto por la jerarquía entre tribunales, como en la existencia de principios comunes que regulan las actuaciones procesales en todas las jurisdicciones. Por ejemplo, los principios de legalidad, contradicción, debido proceso y acceso a la justicia, rigen tanto un proceso laboral como uno penal o administrativo. Así mismo, todas las jurisdicciones están sujetas a la supremacía constitucional y a los límites establecidos por la Corte Constitucional. La unidad del sistema permite también la existencia de mecanismos como el conflicto de competencias y la unificación jurisprudencial, que garantizan coherencia y articulación entre las diferentes ramas del sistema de justicia.")])]),_c('div',{staticClass:"div",attrs:{"titulo":"la indivisibilidad de la jurisdicción"}},[_c('p',[_vm._v("Una vez que un juez asume el conocimiento de un asunto, debe pronunciarse integralmente sobre todas las pretensiones formuladas por las partes, dentro del proceso. No puede emitir decisiones parciales o fragmentarias que dejen aspectos relevantes sin resolver. Este principio responde a la necesidad de brindar seguridad jurídica, evitar sentencias contradictorias y garantizar la economía procesal. Por ejemplo, si una persona demanda el pago de una indemnización por daño emergente y lucro cesante, derivados de un accidente de tránsito, el juez debe pronunciarse sobre ambos conceptos de manera conjunta, valorando las pruebas y estableciendo con claridad la responsabilidad, los montos y la procedencia de cada uno. No podría decidir sobre el daño emergente y guardar silencio sobre el lucro cesante, pues ello vulneraría el derecho a una tutela judicial efectiva y podría generar nuevas controversias innecesarias (Garzón Vallejo, 2007).")])]),_c('div',{staticClass:"div",attrs:{"titulo":"la independencia de la jurisdicción"}},[_c('p',[_vm._v("Es quizá la característica más relevante y protectora del sistema judicial. Esta se refiere a la autonomía funcional, decisional y organizativa que debe tener todo juez o tribunal, frente a cualquier tipo de presión externa. Un juez independiente actúa conforme a su criterio jurídico, sin estar subordinado a las órdenes de otras autoridades, ni a intereses políticos, económicos o particulares. Esta independencia es el sustento de la imparcialidad, la legitimidad de las decisiones y la confianza ciudadana en el sistema de justicia. Un ejemplo se presenta cuando un juez debe resolver un proceso en el que el demandado es una entidad estatal o una figura política reconocida. En ese caso, su decisión no puede estar influenciada por presiones mediáticas ni por instrucciones superiores; debe decidir con base en los hechos, el derecho aplicable y las pruebas allegadas al expediente. ")])])])],1),_vm._m(10)]),_vm._m(11),_vm._m(12),_vm._m(13),_c('p',{attrs:{"data-aos":"fade-down"}},[_vm._v("En la cúspide del sistema judicial colombiano, se encuentran las altas cortes, que cumplen funciones no solo jurisdiccionales, sino también de unificación jurisprudencial y control constitucional:")]),_c('div',{staticClass:"bg-full-width bg-fondo-slider mb-5",attrs:{"data-aos":"fade-right"}},[_c('div',{staticClass:"p-4 p-md-5"},[_c('SlyderA',{staticClass:"bg-white",attrs:{"tipo":"b"}},[_c('div',{staticClass:"row align-items-center p-4 p-md-5"},[_c('div',{staticClass:"col-lg-5 mb-3 mb-lg-0"},[_c('figure',[_c('img',{staticClass:"img-a img-t",attrs:{"src":require("@/assets/curso/temas/24.png")}})])]),_c('div',{staticClass:"col-lg-7 mb-3 mb-lg-0"},[_c('h5',[_vm._v("La Corte Suprema de Justicia ")]),_c('p',[_vm._v("Máxima autoridad de la jurisdicción ordinaria, se divide en salas especializadas Civil, Penal y Laboral y se encarga, entre otras funciones, de conocer los recursos de casación y revisar fallos proferidos por los tribunales superiores. Además, actúa como juez de única instancia en procesos penales contra altos funcionarios del Estado, como el presidente de la República o los ministros, conforme al Artículo 235 de la Constitución (Garzón Vallejo, 2007). ")])])]),_c('div',{staticClass:"row align-items-center p-4 p-md-5"},[_c('div',{staticClass:"col-lg-5 mb-3 mb-lg-0"},[_c('figure',[_c('img',{staticClass:"img-a img-t",attrs:{"src":require("@/assets/curso/temas/25.png")}})])]),_c('div',{staticClass:"col-lg-7 mb-3 mb-lg-0"},[_c('h5',[_vm._v("La Corte Constitucional ")]),_c('p',[_vm._v("Tiene una función principal de control de constitucionalidad. Esta corte revisa las leyes aprobadas por el Congreso para verificar su conformidad con la Carta Política, ejerce el control automático sobre los decretos legislativos expedidos en estados de excepción, y revisa fallos de tutela cuando estos tengan un impacto significativo en la interpretación de los derechos fundamentales.")])])]),_c('div',{staticClass:"row align-items-center p-4 p-md-5"},[_c('div',{staticClass:"col-lg-5 mb-3 mb-lg-0"},[_c('figure',[_c('img',{staticClass:"img-a img-t",attrs:{"src":require("@/assets/curso/temas/26.png")}})])]),_c('div',{staticClass:"col-lg-7 mb-3 mb-lg-0"},[_c('h5',[_vm._v("El Consejo de Estado ")]),_c('p',[_vm._v("Actúa como tribunal supremo de la jurisdicción contencioso-administrativa. Conoce de las controversias entre los particulares y el Estado, resuelve demandas de nulidad contra actos administrativos, y ejerce control sobre la legalidad de normas expedidas por las autoridades administrativas. También cumple funciones consultivas, pues emite conceptos obligatorios sobre proyectos de Ley relacionados con el derecho administrativo y tratados internacionales. ")])])])])],1)]),_vm._m(14),_c('div',{staticClass:"row align-items-start mb-5",attrs:{"data-aos":"fade-left"}},[_vm._m(15),_c('div',{staticClass:"col-lg-8"},[_c('AcordionA',{attrs:{"tipo":"b"}},[_c('div',{staticClass:"div",attrs:{"titulo":"Jueces municipales"}},[_c('p',[_vm._v("Atienden, por lo general, procesos de menor cuantía, asuntos contravencionales, y tutelas interpuestas por ciudadanos. Un ejemplo sería un proceso verbal sumario para reclamar una deuda inferior a 40 salarios mínimos, el cual es competencia del juez municipal civil. También tienen competencia en algunos procesos penales relacionados con contravenciones menores o delitos de menor impacto.")])]),_c('div',{staticClass:"div",attrs:{"titulo":"Jueces del circuito"}},[_c('p',[_vm._v("Conocen asuntos de mayor complejidad o de cuantía más alta, así como procesos relacionados con familia, ejecución de penas, laborales, agrarios, entre otros. Por ejemplo, un juez del circuito penal conoce en primera instancia delitos como el homicidio, mientras que el juez de ejecución de penas y medidas de seguridad es responsable de velar por el cumplimiento de las sanciones impuestas, como la reclusión en establecimiento carcelario o la prisión domiciliaria, y de conceder subrogados penales como la libertad condicional.")])])])],1)]),_vm._m(16),_vm._m(17),_vm._m(18),_c('div',{staticClass:"bg-full-width border-top color-primario"},[_c('div',{staticClass:"p-4 p-md-5"},[_c('h2',{attrs:{"data-aos":"fade-left"}},[_vm._v("MATERIAL COMPLEMENTARIO")]),_c('div',{staticClass:"row material-complementario"},[_c('div',{staticClass:"col-12 col-md-6 col-lg-7"},[_c('p',[_vm._v("Los invitamos a explorar el material complementario de este curso, en esta sección encontrará recursos que le permitirán profundizar y enriquecer su aprendizaje en los temas tratados en esta unidad.")]),_c('p',{staticClass:"d-flex my-4"},[_c('img',{staticClass:"me-3",style:({'max-width':'16px'}),attrs:{"src":require("@/assets/template/book.svg")}}),_c('a',{attrs:{"href":"https://www.scielo.cl/scielo.php?script=sci_arttext&pid=S0718-33992017000100004","target":"_blank","rel":"noopener noreferrer"}},[_vm._v("Nieva Fenoll, Jordi. (2017). Seis conceptos en busca de un objetivo: jurisdicción, acción, proceso, derechos, pena y delito. Política criminal, 12(23), 103-123. ")])]),_c('p',{staticClass:"d-flex my-4"},[_c('img',{staticClass:"me-3",style:({'max-width':'16px'}),attrs:{"src":require("@/assets/template/book.svg")}}),_c('a',{attrs:{"href":"www.redalyc.org/pdf/197/19780115.pdf","target":"_blank","rel":"noopener noreferrer"}},[_vm._v("Cifuentes Muñoz, E., (2002). JURISDICCIÓN CONSTITUCIONAL EN COLOMBI