encia. Proviene del latín jurisdictio, que significa decir el derecho, lo cual implica declarar lo que es conforme a derecho en un caso concreto y hacer que se respete lo decidido (Cifuentes Muñoz, 2006).")])])])])])])},function () {var _vm=this;var _h=_vm.$createElement;var _c=_vm._self._c||_h;return _c('div',{staticClass:"row justify-content-center mb-5"},[_c('div',{staticClass:"col-lg-9 mb-3 mb-lg-0"},[_c('div',{staticClass:"bg-color-8 p-5 mb-4 j1",attrs:{"data-aos":"fade-left"}},[_c('p',[_vm._v("Esta función jurisdiccional "),_c('b',[_vm._v("no se ejerce de manera difusa ni arbitraria")]),_vm._v(". Por el contrario, está institucionalizada. Se encuentra en manos de jueces y tribunales legalmente establecidos, que actúan conforme a procedimientos previamente definidos en el ordenamiento jurídico.")]),_c('p',{staticClass:"mb-0"},[_vm._v("En Colombia, esta facultad está reconocida expresamente en el Artículo 116 de la Constitución Política, que establece que la jurisdicción "),_c('b',[_vm._v("la ejercen los jueces de la República")]),_vm._v(" y, en determinados casos, algunos particulares autorizados por la ley como árbitros o conciliadores.")])]),_c('p',{attrs:{"data-aos":"fade-down"}},[_vm._v("La función jurisdiccional comprende dos actos fundamentales: ")])]),_c('div',{staticClass:"col-lg-3"},[_c('figure',[_c('img',{staticClass:"img-a img-t",attrs:{"src":require("@/assets/curso/temas/12.png"),"data-aos":"zoom-in"}})])])])},function () {var _vm=this;var _h=_vm.$createElement;var _c=_vm._self._c||_h;return _c('div',{staticClass:"bg-full-width-1 bg-fondo-1 py-4"},[_c('div',{staticClass:"px-4 px-md-5"},[_c('div',{staticClass:"row justify-content-center align-items-stretch text-center mb-4"},[_c('div',{staticClass:"col-lg-6 mb-4",attrs:{"data-aos":"zoom-in-up"}},[_c('div',{staticClass:"custom-image-card-2 h-100 br-1"},[_c('img',{staticClass:"custom-image-card__image",attrs:{"src":require("@/assets/curso/temas/13.png"),"alt":""}}),_c('div',{staticClass:"custom-image-card__text p-4"},[_c('h5',{staticClass:"mb-2 text-center"},[_vm._v("Juzgar")]),_c('p',{staticClass:"mb-0"},[_vm._v("Se refiere a la potestad del juez para analizar los hechos de un caso, interpretarlos jurídicamente y emitir una sentencia que resuelva el conflicto.")])])])]),_c('div',{staticClass:"col-lg-6 mb-4",attrs:{"data-aos":"zoom-in-down"}},[_c('div',{staticClass:"custom-image-card-2 h-100 br-1"},[_c('img',{staticClass:"custom-image-card__image",attrs:{"src":require("@/assets/curso/temas/14.png"),"alt":""}}),_c('div',{staticClass:"custom-image-card__text p-4"},[_c('h5',{staticClass:"mb-2 text-center"},[_vm._v("Hacer ejecutar lo juzgado")]),_c('p',{staticClass:"mb-0"},[_vm._v("Implica que esa decisión no es una simple declaración de voluntad, sino una resolución que debe cumplirse, y que el estado está legitimado para hacerla efectiva, incluso mediante la coerción.")])])])])]),_c('div',{staticClass:"row mb-5"},[_c('div',{staticClass:"col-lg-3 mb-3 mb-lg-0"},[_c('figure',[_c('img',{staticClass:"img-a img-t",attrs:{"src":require("@/assets/curso/temas/15.png"),"data-aos":"zoom-in"}})])]),_c('div',{staticClass:"col-lg-9 j1"},[_c('p',{attrs:{"data-aos":"fade-down"}},[_vm._v("Este poder del Estado no es absoluto. Está regulado por un conjunto de normas jurídicas que aseguran que su ejercicio sea imparcial, legal, y respetuoso de los derechos fundamentales. De ahí que la jurisdicción solo se ejerza a través del proceso judicial, donde las partes tienen oportunidad de presentar pruebas, controvertir argumentos, y ser oídas ante un juez independiente. El respeto por principios como el debido proceso, la igualdad de las partes, la contradicción y la legalidad procesal, son indispensables para que la función jurisdiccional sea legítima.")]),_c('div',{staticClass:"bg-color-5 dot-1 p-4",attrs:{"data-aos":"fade-left"}},[_c('p',{staticClass:"mb-0"},[_vm._v("Por ejemplo, si una persona es víctima de un incumplimiento contractual, como cuando un comprador no paga lo pactado en la compraventa de un bien inmueble, esa persona puede acudir a un juez civil. Este, al ejercer la jurisdicción, analizará el contrato, evaluará las pruebas y decidirá si procede o no, ordenar el pago o la restitución del bien. Si el juez ordena pagar y la parte vencida no cumple voluntariamente, el mismo aparato judicial puede ordenar embargos o ejecuciones, para que se haga efectiva la sentencia. ")])])])])])])},function () {var _vm=this;var _h=_vm.$createElement;var _c=_vm._self._c||_h;return _c('div',{staticClass:"row mb-5"},[_c('div',{staticClass:"col-lg-8 mb-3 mb-lg-0 j1"},[_c('p',{attrs:{"data-aos":"fade-down"}},[_vm._v("En este sentido, la jurisdicción es la vía institucional que convierte los derechos reconocidos por el Derecho Sustancial en realidades tangibles. Sin ella, los derechos quedarían en el papel, pues no existiría una autoridad imparcial y competente que los hiciera valer frente a los incumplimientos o violaciones. La jurisdicción, entonces, es condición necesaria para que haya justicia, dentro de una sociedad organizada.")]),_c('div',{staticClass:"bg-color-9 p-4",attrs:{"data-aos":"fade-left"}},[_c('p',{staticClass:"mb-0"},[_vm._v("Además, el ejercicio de la jurisdicción también cumple una función pacificadora y estabilizadora. En lugar de que los conflictos se resuelvan por la fuerza, se canalizan por vías jurídicas, lo cual evita la anarquía y consolida el orden público. Por eso, se afirma que el juez es el órgano por excelencia, que transforma los conflictos sociales en decisiones jurídicas con valor normativo individual (Cifuentes Muñoz, 2006). ")])])]),_c('div',{staticClass:"col-lg-4"},[_c('figure',[_c('img',{staticClass:"img-a img-t",attrs:{"src":require("@/assets/curso/temas/16.png"),"data-aos":"zoom-in"}})])])])},function () {var _vm=this;var _h=_vm.$createElement;var _c=_vm._self._c||_h;return _c('div',{staticClass:"row align-items-center mb-5"},[_c('div',{staticClass:"col-lg-auto",attrs:{"data-aos":"fade-left"}},[_c('img',{staticClass:"